
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import {time} from '../config'
import {mapMutations} from 'vuex'
export default {
  name: 'password',
  head: {
    title: {
      inner: '忘记密码'
    }
  },
  components: {
    Group,
    XInput,
    XButton,
    Countdown
  },
  data () {
    return {
      loading: false, // 注册按钮显示加载
      getting: false, // 获取验证码显示加载
      show: false, // 倒计时
      start: false,
      time: time,
      smsType: 1,
      text: '发送验证码',
      pwd: '',
      form: {
        tel: '',
        pwd: '',
        captcha: ''
      }
    }
  },
  methods: {
    handleTips () {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        position: 'bottom',
        text: this.time + '秒后才能重新获取！',
        time: '1000'
      })
    },
    postSMS () {
      this.handleSms(this)
    },
    handleSubmit () {
      this.handleEditPassword(this)
    },
    handleTime () {
      this.show = false
      this.start = false
      this.time = time
    },
    handleSendSms () {
      this.postSMS(this)
    },
    ...mapMutations({
      handleSms: 'postSMS',
      handleEditPassword: 'postEditPassword'
    })
  }
}



